import { useEffect, useState } from 'react';

export const useAIOpponent = turn => {
  const [aiChoice, setAIChoice] = useState('');

  useEffect(() => {
    if (turn === 1) {
      const options = ['attack', 'special', 'heal', 'attack2'];
      setAIChoice(options[Math.floor(Math.random() * options.length)]); //choose between the 4 attack options
    }
  }, [turn]);

  return aiChoice;
};
